import { ADD_TO_BASKET, REMOVE_FROM_BASKET } from "../actions/types";

export const addToBasket = ({ id, title, image, price, rating }) => (
  dispatch
) => {
  dispatch({
    type: ADD_TO_BASKET,
    payload: {
      id: id,
      title: title,
      image: image,
      price: price,
      rating: rating,
    },
  });
};

export const removeFromBasket = (id) => (dispatch) => {
  console.log(
    dispatch({
      type: REMOVE_FROM_BASKET,
      payload: id,
    })
  );
};
